.container {
    margin-bottom: var(--triple-unit);
    border-radius: var(--quadruple-unit);
    box-shadow: var(--box-shadow);
    overflow: hidden;
}
.writer {
    display: flex;
    align-items: center;
    padding: var(--triple-unit) var(--quadruple-unit);
}
.writer > span {
    margin-left: var(--double-unit);
    font-weight: 600;
}
.image_wrap {
    position: relative;
    height: 328px;
    overflow: hidden;
}
.image_wrap > img {
    width: 100%;
}
.recipe_count {
    position: absolute;
    bottom: var(--double-unit);
    right: var(--double-unit);
    width: 27px;
    height: 18px;
    background: rgba(255, 255, 255, 0.6);
    border-radius: 5px;
    text-align: center;
    overflow: hidden;
}
.recipe_count > img,
.recipe_count > span {
    vertical-align: middle;
}
.recipe_count > img {
    margin-right: 1px;
}
.recipe_count > span {
    font-size: 12px;
    font-weight: 500;
    color: var(--dim-gray);
}
.icon_wrap > li {
    display: inline-block;
    margin-right: var(--triple-unit);
    cursor: pointer;
}
.icon_wrap > li > img,
.icon_wrap > li > span {
    vertical-align: middle;
}
.icon_wrap > li > span {
    margin-left: 2px;
    font-weight: 600;
}
.content_wrap {
    padding: var(--triple-unit) var(--quadruple-unit);
}
.content {
    margin: var(--triple-unit) 0;
}
.more_button {
    margin-left: var(--double-unit);
    font-size: 12px;
    color: var(--silver);
    cursor: pointer;
}
.date {
    font-size: 12px;
    color: var(--silver);
}